<template>
  <div id="caic-date-picker">
    <van-cell-group>
      <van-field v-model="effectiveDate"
                 :readonly='true'
                 :border='border'
                 :input-align="inputAlign"
                 :disabled="disabled"
                 @click="selDate">
        <div slot="label"
             :class="{'date-title':boldTitle}"></div>
        <div slot="right-icon"
             :class="{'display-none':!isSel}"
             @click="selDate">
          <slot name="right-icon">
            <span @click="selDate">
              <svg class="icon svgIcon"
                   aria-hidden="true">
                <use xlink:href="#icon-date"></use>
              </svg>
            </span>
          </slot>
        </div>
      </van-field>
    </van-cell-group>
    <van-popup v-model="selDatePopup"
               position="bottom"
               :class="{'display-none':!isSel}">
      <van-datetime-picker v-model="defaultPickerDate"
                           :type='type'
                           :min-date='minDate'
                           :max-date='maxDate'
                           :min-hour='minHour'
                           :max-hour='maxHour'
                           :min-minute='minMinute'
                           :max-minute='maxMinute'
                           :item-height='itemHeight'
                           :confirm-button-text='confirmButtonText'
                           :cancel-button-text='cancelButtonText'
                           :visible-item-count='visibleItemCount'
                           @cancel='selDateCancel'
                           @confirm="selDateSure"
                           @change="selDateChange"
                           class="sel-date-picker">
      </van-datetime-picker>
    </van-popup>
  </div>

</template>
<script>
/**
 * **********************done*********************
 * 
 *  1.minDate (Date) 
 *  2.maxDate (Date) 
 *  3.minHour Number  time   0
 *  4.maxHour Number  time   23
 *  5.minMinute Number  time   0
 *  6.maxMinute Number  time   59
 *  7.dateFormateRule (String) 
 *  8.type (String)  date-time date time year-month date-time
 *  9.itemHeight Number  44
 *  10.confirmButtonText String   
 *  11.cancelButtonText String 	
 *  12.visibleItemCount Number  5
 *  13.defaultDate(Date) 
 *  14.isSel (Boolean) true
 *  15.border Boolean  true
 *  16.labelAlign String left center right --left
 *  17.inputAlign String left center right  --left
 *  18.disabled Boolean  --false
 *  19.boldTitle (boolean) title false
 *  20.v-model (String) 
 * 
 *  21.@confirm() Function   dateObject--, formatedDate--
 *  22.@cancel() Function 
 *  23.@change() Function   picker 
 * 
 *  24.right-icon 
 * ===================doing==========
 *  1.spanTime (Object)
 * *******************except***************
 *  spanTime 
 * 
 */
import Vue from 'vue'
import {
  Popup,
  DatetimePicker,
  CellGroup,
  Field,
} from 'vant'
Vue.use(DatetimePicker).use(Popup).use(Field).use(CellGroup)
import '../../font/iconfont.js'
// import { format } from 'date-fns'
import dayjs from 'dayjs'
export default {
  // inheritAttrs: false,
  name: 'caic-date-picker',
  props: {
    //v-model 
    value: {
      type: String,
      required: true
    },
    // label
    boldTitle: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: true
    },
    labelAlign: {
      type: String,
      default: 'left'
    },
    inputAlign: {
      type: String,
      default: 'left'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    // , true
    isSel: {
      type: Boolean,
      default: true
    },
    // dateTimePicker 
    defaultDate: {
      type: Date,
      default: new Date()
    },
    // van-datetime-picker
    minDate: Date,
    // van-datetime-pickerendDate  spanTime 
    maxDate: Date,
    minHour: Number,
    maxHour: Number,
    minMinute: Number,
    maxMinute: Number,
    itemHeight: {
      type: Number,
      default: 44
    },
    confirmButtonText: {
      type: String,
      default: ''
    },
    cancellButtonText: {
      type: String,
      default: ''
    },

    // 
    dateFormateRule: {
      type: String,
      default: 'YYYY-MM-DD'
    },
    visibleItemCount: {
      type: Number,
      default: 5
    },
    type: {
      type: String,
      default: 'date'
    },

  },
  data () {
    return {
      effectiveDate: null,
      selDatePopup: false,
      defaultPickerDate: this.defaultDate
    }
  },
  created () {
    // this.effectiveDate = format(this.defaultValue, this.dateFormateRule)
    this.effectiveDate = dayjs(this.value).format(this.dateFormateRule)
    // this.returnMsg(this.defaultValue, this.effectiveDate)
  },
  watch: {
    effectiveDate: function () { this.$emit('input', this.effectiveDate) }
  },
  methods: {
    // 
    returnMsg: function (dateObject, formatedDate) {
      this.$emit('confirm', dateObject, formatedDate)
    },
    selDate: function () {
      if (this.isSel) {
        this.selDatePopup = true
      }
    },
    selDateCancel: function () {
      this.selDatePopup = false
      this.$emit('cancel')
    },
    selDateSure: function (value) {
      this.selDatePopup = false
      // this.effectiveDate = format(value, this.dateFormateRule)
      this.effectiveDate = dayjs(value).format(this.dateFormateRule)
      this.returnMsg(value, this.effectiveDate)
      // console.log(this.defaultPickerDate)
    },
    selDateChange: function (picker) {
      this.$emit('change', picker)
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../css/caic.styl'

#caic-date-picker
  .van-cell
    changeFontSize(14px)

  .date-title
    font-weight: 600
    changeFontSize(15px)

  .icon
    width: 1em
    height: 1em
    vertical-align: -0.15em
    fill: currentColor
    overflow: hidden
    changeFontSize(20px)
    color: #333

  .display-none
    display: none
</style>



